.App {
  text-align: center;
}

button {
  height: 65px;
  border: none;
  border-radius: 5px;
  background-color: #eeea;
  color: #223749;
  font-weight: bold;
  font-size: 1.15em;
  -webkit-transition: 100ms;
  -o-transition: 100ms;
  transition: 100ms;
  max-width: 20vw;
  min-width: 250px;
  margin: 7px;
}

button:hover {
  background-color: #eeed;
  -webkit-box-shadow: 0px 0px 30px #eee;
          box-shadow: 0px 0px 30px #eee;
}

input {
  height: 30px;
  width: 20vw;
  margin: 8px;
  text-align: center;
  font-size: 18px;
  border-radius: 2px;
  color: black
}

::-webkit-input-placeholder {
  font-weight: bold;
  color: #372E2D;
}

:-ms-input-placeholder {
  font-weight: bold;
  color: #372E2D;
}

::-ms-input-placeholder {
  font-weight: bold;
  color: #372E2D;
}

::placeholder {
  font-weight: bold;
  color: #372E2D;
}



